.raiting {
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__stars--active > :nth-child(-n + 4) {
    background-image: url(../images/star-active.svg);
    margin-right: 4px;
  }
  
  &__feedbacks {
    font-size: 10px;
    line-height: 14px;
    font-weight: 400;
    color: $dark-font-color;
  }
}
